import '../../App.css'

function TabelaFome() {

  return (
        <div>
            <table style={{"width":"94%", "text-align":"center", "margin":"auto"}}>
            <tr style={{"background-color":"#c8dff5"}}>
            <th style={{"width":"7%"}}>Tempo<br/>de<br/>Fome</th>
            <th style={{"width":"86%"}}>Consequncias ruins</th>
            <th style={{"width":"7%"}}>Risco de vida<br/>da pessoa<br/>desamparada</th>
            </tr>
            <tr>
            <td>0-3<br/>horas</td>
            <td>Mudana mnima</td>
            <td>0</td>
            </tr>
            <tr style={{"background-color":"#c8dff5"}}>
            <td>4-8<br/>horas</td>
            <td style={{"padding":"3%"}}>Voc vai sentir mais fome e sua barriga vai doer um pouco, e pode ter dor de cabea</td>
            <td>0</td>
            </tr>
            <tr>
            <td>9-12<br/>horas</td>
            <td style={{"padding":"3%"}}>Voc vai comear a se sentir cansado e rabugento, raivoso, irritado, estressado e vai ter dor de cabea</td>
            <td>baixa</td>
            </tr>
            <tr style={{"background-color":"#c8dff5"}}>
            <td>13-16<br/>horas</td>
            <td style={{"padding":"3%"}}>vai ser mais difcil prestar ateno ou se concentrar</td>
            <td>moderada</td>
            </tr>
            <tr>
            <td>17-24<br/>horas</td>
            <td style={{"padding":"3%"}}>Seu corpo vai no ter acar suficiente, o que vai fazer voc se sentir tonto ou TREMENDO</td>
            <td>moderada</td>
            </tr>
            <tr style={{"background-color":"#c8dff5"}}>
            <td>25-48<br/>horas</td>
            <td style={{"padding":"3%"}}>Voc vai se sentir fraco e entra em situao de alto estresse e em modo de sobrevivncia seu corao vai bater mais rpido pois falta energia que vem da comida, ento tenta trabalhar dobrado na tentativa de manter o resultado (entrega de oxignio e nutrientes para orgo vitais)</td>
            <td>alta</td>
            </tr>
            <tr>
            <td>49-72<br/>horas</td>
            <td style={{"padding":"3%"}}>Seu corpo vai comear a usar energia armazenada (gordura,...), o que vai fazer voc se sentir cansado e seu sistema imunolgico vai no funcionar to bem, fica doente mais facilmente</td>
            <td>alta</td>
            </tr>
            <tr style={{"background-color":"#c8dff5"}}>
            <td>3-7<br/>dias</td>
            <td style={{"padding":"3%"}}>Os msculos passam a serem consumidos como energia, voc vai se sentir muito fraco e seus msculos vo diminuir. Isso no  bom para o seu corpo, danifica o organismo. Diminuio da motivao ou produtividade</td>
            <td>alta</td>
            </tr>
            <tr>
            <td>8-14<br/>dias</td>
            <td style={{"padding":"3%"}}>Seus rgos, que so partes importantes do seu corpo, vo ficar feridos e voc vai ficar doente muito mais facilmente</td>
            <td>muito alta</td>
            </tr>
            <tr style={{"background-color":"#c8dff5"}}>
            <td>15-21<br/>dias</td>
            <td style={{"padding":"3%"}}>Voc vai ficar muito doente e sua vida vai estar em perigo</td>
            <td>extremamente alto</td>
            </tr>
            <tr>
            <td>22+<br/>dias</td>
            <td style={{"padding":"3%"}}>Voc est em perigo extremo porque seu corpo no est recebendo comida suficiente, e seus rgos vo parar de funcionar corretamente ou parar a qualquer momento</td>
            <td>extremamente alto</td>
            </tr>
            </table>
        </div>
  );
}

export default TabelaFome;
